	    	left: 0;
	    }

	   	@media (min-width: 992px) {
	    	top: -60px;
	    }

	    @media (min-width: 1100px) {
	    	top: -80px;
	    }
	    @media (min-width: 1280px) {
	    	top: -140px;
	    }
